<template>
  <div class="row" id="translateForm">
    <div class="col-md-6 col-md-offset-3">
      <form class="well form-inline" v-on:submit="formSubmit">
        <input id="input" class="form-control" type="text" v-model="textToTranslate" placeholder="请输入需要翻译的内容">
        <select id="language" class="form-control" v-model='language'>
          <option value="en">English</option>
          <option value="zh">Chinese</option>
          <option value="ru">Russian</option>
          <option value="ko">Korean</option>
          <option value="ja">Janpenese</option>
        </select>
        <input id="submit" class="btn btn-primary btn-sm" type="submit" value="翻译">
      </form>
    </div>
  </div>
</template>

<script>

export default {
  name: 'translateForm',
  data:function(){
    return{
      textToTranslate:"",
      language:"en"
    }
  },
  methods:{
    formSubmit:function(e){
      e.preventDefault();
      this.$emit("formSubmit",this.textToTranslate,this.language);
      // alert(this.textToTranslate);
    }
  }
}
</script>

<style>
#input,#language,#submit{
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 20px
}
</style>



