<template>
  <div class="well col-md-6 col-md-offset-3" id="translateOutput">
        <h2>{{translatedText}}</h2>
  </div>
</template>

<script>

export default {
  name: 'translateOutput',
  props:[
  "translatedText"
  ]
}
</script>

<style>
@media screen and (min-width: 992px) {
  #translateOutput{
    max-width: 50%;
  }
}
@media screen and (max-width: 991px) {
  #translateOutput{
    max-width: 100%;
  }
}

</style>



